.additionals-main-container {
   display: flex;
   flex-direction: row;
   align-items: center;
   justify-content: center;
}

.additionals-container {
   flex: 1;
   display: flex;
   flex-direction: row;
   flex-wrap: wrap;
   justify-content: center;
   min-width: 500px;
}


@media only screen and (max-width: 600px) {
   .additionals-main-container {
      position: relative;
   }
   .additionals-person-image {
      position: absolute;
      height: 100%;
      opacity: 0.6;
   }
   .additionals-container {
      z-index: 1;
      flex-direction: column;
      width: 20%;
   }
}